<template>
  <div class="rightSide">
    <loading :active.sync="isLoading"></loading>
    <h2></h2>
    <div class="divider"></div>
    <div class="giveWrap">
      <img :src="orderProduct.imageUrl" alt />
      <div class="divider giveLine"></div>
      <div class="giveDetail">
        <div class="getngive">
          <span></span>
          <span>{{orderPoster.name}}</span>
        </div>
        <div class="getngive">
          <span></span>
          <span>{{orderProduct.create_time | time}}</span>
        </div>
        <div class="getngive">
          <span>LINE ID</span>
          <span>{{orderPoster.lineID}}</span>
        </div>
        <div class="getngive">
          <span></span>
          <span>{{getOrderID() }}</span>
        </div>
        <div class="getngive">
          <span></span>
          <span>{{orderProduct.title}}</span>
        </div>
        <div class="getngive">
          <span></span>
          <span class="storySpan">{{orderRequester.message}}</span>
        </div>
        <div class="getngive">
          <span></span>
          <span>{{getOrderStatus()}}</span>
        </div>
      </div>
      <div class="getBtns">
        <button class="noRequestBtn" @click="noRequest"></button>
        <button class="checkGetOrderBtn" @click="back"></button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      orderRequester: {},
      orderProduct: {},
      orderPoster: {},
      orderID: "",
      orderStatus: "",
      isLoading: true,
      requestBTN: {
        submit: "" // 0 | 1 | 2 | 3
      }
    };
  },
  methods: {
    getGoodsDetail() {
      let apiUrl = `${process.env.APIPATH}/api/orders`;
      let vm = this;
      this.$http.get(apiUrl).then(response => {
        let getorderID = this.$route.params.goods_id; // id
        // id 
        console.log(getorderID);
        console.log(response.data);
        response.data.orders.forEach(product => {
          console.log(product.id);
          if (getorderID == product.id) {
            console.log("product.id", product.id);
            let getOrderInfo = Object.assign({}, product);
            console.log("getOrderInfo", getOrderInfo);
            vm.orderRequester = getOrderInfo.requester;
            vm.orderProduct = getOrderInfo.product;
            vm.orderID = getOrderInfo.id;
            vm.orderStatus = getOrderInfo.status;
            vm.orderPoster = getOrderInfo.poster;
          }
        });
      });
    },
    getLogInStatus() {
      const apiUrl = `${process.env.APIPATH}/api/login`;
      const vm = this;
      this.$http.get(apiUrl).then(response => {
        console.log("login", response.data);
        if (!response.data.success) {
          this.$router.push("/home");
        }
      });
    },
    getOrderID() {
      let orderID = this.orderID.split("-", 1);
      return orderID[0];
    },
    getOrderStatus() {
      if (this.orderStatus == "0") {
        return "";
      }
      if (this.orderStatus == "1") {
        return "";
      }
      if (this.orderStatus == "2") {
        return "";
      }
      if (this.orderStatus == "3") {
        return "";
      }
    },
    back() {
      this.$router.go(-1); //
    },
    // 
    noRequest() {
      let vm = this;
      let id = vm.orderID;
      let apiUrl = `${process.env.APIPATH}/api/order/${id}`;
      vm.requestBTN.submit = "0";
      this.$swal({
        title: "?",
        text: "",
        type: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: ""
      }).then(result => {
        if (result.value) {
          vm.$http.put(apiUrl, vm.requestBTN).then(response => {
            console.log("response.data", response.data);
            if (response.data.success) {
              this.$swal("!", "", "success");
              this.getGoodsDetail();
              this.$router.push("/admin/getorder");
            }
          });
        }
      });
      if (vm.orderStatus == "0") {
        this.$swal({
          type: "error",
          title: "Oops",
          text: ""
        });
      }
      if (vm.orderStatus == "1") {
        this.$swal({
          type: "error",
          title: "Oops",
          text: ""
        });
      }
      if (vm.orderStatus == "3") {
        this.$swal({
          type: "error",
          title: "Oops",
          text: ""
        });
      }
      this.$router.push("/admin/getorder");
    }
  },
  mounted() {
    setTimeout(() => {
      this.isLoading = false;
    }, 2000);
  },
  created() {
    this.getGoodsDetail();
    this.getOrderID();
    this.getLogInStatus();
    this.getOrderStatus();
  },
  //
  filters: {
    time(value) {
      let time = new Date(value);
      let newTime = time.toString();
      console.log(newTime.split(" ", 4).join(" "));
      return newTime.split(" ", 4).join(" ");
    }
  }
};
</script>


